// Bootstrap
button:active, button:focus, button:hover{
    outline: none !important;
}

.card {
    margin: 0px 5px 10px 5px !important;
    padding: 0px !important;
    border-radius: 0px !important;
    border: none;
}

.card-header_type1 {
    background-color: $color5;
    color: $color6;
    font-weight:bold;
    font-size: $fontsize7;
    margin: 0px !important;
    padding: 5px 0 5px 0 !important;
    text-align: center !important;
    border-radius: 0px !important;
    border: none;
}

.card-body_type1 {
    border: 0.5px solid $color5 !important;
    padding: 5px 5px 15px 5px !important;
    background-color: $color2;
    color: $color7;
    font-size: $fontsize5;
}

.card-footer_type1 {
    color: $color4;
    background-color: $color6;
    border-top: 0 !important;
    border-right: 0.5px solid $color5 !important;
    border-bottom: 0.5px solid $color5 !important;
    border-left: 0.5px solid $color5 !important;
    font-size: $fontsize6;
    padding: 5px !important;
    border-radius: 0px !important;
    border: none;
}

.card-header_type2 {
    background-color: $color4 !important;
    color: $color1;
    font-size: $fontsize5;
    margin: 0px !important;
    padding: 5px !important;
    text-align: left !important;
}

.card-body_type2 {
    border: 0.5px solid $color4 !important;
    padding: 3px !important;
    background-color: $color6;
    color: $color4;
    font-size: $fontsize5;
}

.navbar {
    margin-bottom: 5px !important;
    padding: 0 5px 0 10px !important;
    background-color: $color3 !important;
}

.navbar-brand {
    margin: 0 !important;
}

.nav-link {
    color: $color8 !important;
    font-size: $fontsize5;
}

.row{
    padding: 2px !important;
}

// Images

.img_flag {
    height: 1.2rem;
    margin-right: 3px !important;
}

.img_logo {
    height: 5rem;    
}

.img_answer {
    display: block;
    margin: auto;
    padding: 5px 0 5px 0 !important;
    max-width: 80%;
}

// Divs

.div_source {
    text-align: right;
    font-size: $fontsize4 !important;
    font-weight: bold;
    padding: 5px 0 5px 0 !important;
}

.div_empty {
    height: 1rem !important;
}

.div_sempty {
    height: 0.5rem !important;
}
  
.div_ssempty {
    height: 0.25rem !important;
}
  
.div_sssempty {
    height: 0.12rem !important;
} 

.div_maintitle {
    font-size: $fontsize8;
    color: $color4;
    font-weight: 700 !important;
}

.div_submaintitle {
    font-size: $fontsize5;
    color: $color4;
}

/********************************* Wait for Angular "Loading..." circle **************************/
.angular_loading {
    border: 3px solid $color6;
    border-radius: 50%;
    border-top: 3px solid $color5;
    width: 20px !important;
    height: 20px !important;
    animation: spin 2s linear infinite;
}
@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}