/********************************* Wait for Angular "Loading..." circle **************************/
.angular_loading {
  border: 3px solid #a8d0e6;
  border-radius: 50%;
  border-top: 3px solid #5085a5;
  width: 20px !important;
  height: 20px !important;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
